body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header[role="banner"] #logo-main {
  display: block;
  margin: 20px auto;
}
#navbar-primary.navbar-default {
  background: transparent;
  border: none;
}
#navbar-primary.navbar-default .navbar-nav {
  width: 100%;
  text-align: center;
}
#navbar-primary.navbar-default .navbar-nav > li {
  display: inline-block;
  float: none;
}
#navbar-primary.navbar-default .navbar-nav > li > a {
  padding-left: 30px;
  padding-right: 30px;
}

.footer {
  /* position: absolute; */
  /* bottom: 0; */
  margin-top: 50px;
  width: 100%;
  height: 60px;
  background-color: #f5f5f5;
}
.container {
  width: auto;
  max-width: 680px;
  padding: 0 15px;
}

.container .text-muted {
  margin: 20px 0;
}

.starter-template {
  padding: 40px 15px;
  text-align: center;
}

img {
  max-width: 100%;
  height: auto;
}

/*==== ARRASTRAR Y SOLTAR ====*/
.drop,
.drop__container {
  display: grid;
}

.drop {
  /* align-items: center;
  justify-content: center;
  margin-top: 4rem; */
}

.drop__container {
  align-items: center;
  justify-content: center;
  row-gap: 1rem;
  padding: 2rem;
  box-shadow: 4px 4px 16px #e1e1e1;
}

.drop__info {
  text-align: center;
}

.drop__card,
.drop__data {
  display: flex;
  align-items: center;
}

.drop__card {
  width: 360px;
  justify-content: space-between;
  padding: 0.75rem 1.25rem 0.75rem 0.75rem;
  background-color: var(--first-color-lighten);
  box-shadow: 4px 4px 16px #e1e1e1, -2px -2px 16px #fff;
  border-radius: 2.5rem;
  margin-bottom: 2rem;
}

.drop__img {
  width: 55px;
  height: 55px;
  border-radius: 50%;
  margin-right: 1rem;
}

.drop__name {
  font-size: var(--normal-font-size);
  color: var(--first-color);
  font-weight: 500;
}

.drop__content {
  padding-left: 3rem;
}
.drop__icons {
  padding-right: 3rem;
}

.drop__profession {
  font-size: var(--smaller-font-size);
  color: var(--first-color-light);
}

.drop__social {
  margin: 0 0.375rem;
  color: var(--first-color-light);
  transition: 0.4s;
}

.drop__social:hover {
  color: var(--first-color);
}
.checkbox label,
.radio label {
  min-height: 20px;
  padding-left: 20px;
  margin-bottom: 0;
  font-weight: 400;
  cursor: pointer;
}

label {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: 700;
}

.overflow {
  width: 15em;
  white-space: nowrap;
  overflow: hidden;
}
.ellipsis {
  text-overflow: ellipsis;
}

.liked {
  color: #fe938c
}
